.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 1s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.app-container {
  height: 100vh; /* Set the height of the container to 100% of the viewport height */
  display: flex;
  flex-direction: column;
  background: linear-gradient(to top, rgb(156, 130, 239), rgb(7, 7, 35));
}

.row {
  flex: 1; /* Make each row take equal vertical space */
  display: flex;
}

.column {
  flex: 1; /* Make each column take equal horizontal space */
  padding: 4%; /* Set spacing inside the container */
  border: 1px solid #fff;
}

.card {
  border-radius: 10%; /* Set rounded corners */
  width: 100%; /* Take up the full width of the parent */
  height: 100%; /* Take up the full height of the parent */
  background: linear-gradient(to right, rgb(156, 130, 239), rgb(127, 92, 239));
  color: white;
  align-items: center;
  font-size: 5em; /* Set the font size to be larger */
  text-align: center; /* Center the text horizontally */
  box-sizing: border-box; /* Include padding and border in the total width and height */
  display: flex;
  justify-content: center;
  align-items: center;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
